import { Component, OnInit, ViewEncapsulation, Inject } from '@angular/core';
import { ActivatedRoute, Router, NavigationEnd } from '@angular/router';
import { DOCUMENT } from '@angular/common';

import { CondoAnimations } from 'src/@condo/animations';

import { ApartmentService } from 'src/app/api/controllers/Apartment';

import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';

import { CondoConfigService } from 'src/@condo/services/config/config.service';
import { Layout } from 'src/app/layout/layout.types';
import { AppConfig, Theme } from 'src/app/core/config/app.config';


@Component({
  selector: 'app-complete',
  templateUrl: './complete.component.html',
  styleUrls: ['./complete.component.scss'],
  encapsulation: ViewEncapsulation.None,
  animations   : CondoAnimations
})
export class CompleteComponent implements OnInit {

  config: AppConfig;
  layout: Layout;
  theme: Theme;

  // Private
  private _unsubscribeAll: Subject<any>;

  constructor(
    private _activatedRoute: ActivatedRoute,
    private _router: Router,
    @Inject(DOCUMENT) private _document: any,
    private _condoConfigService: CondoConfigService,
  ) { 

    // Set the private default
    this._unsubscribeAll = new Subject();

  }

  

  ngOnInit(): void {

    // Subscribe to config changes
    this._condoConfigService.config$
    .pipe(takeUntil(this._unsubscribeAll))
    .subscribe((config: AppConfig) => {

      // Store the config
      this.config = config;

      // Store the theme
      this.theme = config.theme;

      document.querySelector('body').className = '';
      this._document.body.classList.add('condo-splash-screen-hidden');

      // Update the selected theme class name on body
      const themeName = 'condo-theme-' + config.theme;
      this._document.body.classList.add(themeName);

    });

    this._router.navigate(['/user/payment']);
  }

}
